import React, { Component, Fragment } from 'react';
import { Row, Table, Col, Button } from 'antd';
import { subsidyObject } from 'dump/data';
import FormSearchAddStudent from './FormSearchAddStudent';

const table1 = [
  {
    title: 'Thao tác',
    dataIndex: 'thaotac',
    render: () => (
      <Fragment>
        <Button type="primary">Chọn</Button>
      </Fragment>
    ),
  },
  {
    title: 'Mã SV',
    dataIndex: 'code',
    key: 'code',
  },
  {
    title: 'Họ tên',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Ngày sinh',
    dataIndex: 'birthdate',
    key: 'birthdate',
  },
  {
    title: 'Lớp',
    dataIndex: 'class',
    key: 'class',
  },
];
const table2 = [
  {
    title: 'Thao tác',
    dataIndex: 'thaotac',
    render: () => (
      <Fragment>
        <Button type="danger">Xóa</Button>
      </Fragment>
    ),
  },
  {
    title: 'Mã SV',
    dataIndex: 'code',
    key: 'code',
  },
  {
    title: 'Họ tên',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Ngày sinh',
    dataIndex: 'birthdate',
    key: 'birthdate',
  },
  {
    title: 'Lớp',
    dataIndex: 'class',
    key: 'class',
  },
];
export default class ExamOrganization extends Component {
  state = {};
  render() {
    return (
      <div>
        <Row>
          <Col span={24}>
            <FormSearchAddStudent />
          </Col>
        </Row>
        <Row>
          <Row>
            <Col span={24}>
              <Table bordered dataSource={subsidyObject} columns={table1} />
            </Col>
            <Col span={24}>
              <Table bordered dataSource={subsidyObject} columns={table2} />
            </Col>
          </Row>
        </Row>
      </div>
    );
  }
}



