import React, { Component } from 'react';
import { Row, Col, Form, Input, Select } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import YearSemesterInput from 'components/common/input/YearSemesterInput';

const { Option } = Select;

@Form.create({ name: 'FormSearchStudent' })
class FormSearchStudent extends Component {
  state = {};
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} />
          <YearSemesterInput form={this.props.form} />

          <Col span={8}>
            <Form.Item label="Lọc theo">
              {getFieldDecorator('system613', {})(
                <Select>
                  <Option value="1">Thi lần 1</Option>
                  <Option value="2">Thi lại</Option>
                  <Option value="3">Học lại</Option>
                  <Option value="4">Tốt nghiệp</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Mã SV">
              {getFieldDecorator('email', { initialValue: '' })(<Input />)}
            </Form.Item>
          </Col>
          <Col span={24} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <BtnSearch />
              <BtnClear />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormSearchStudent;



