import React, { Component } from 'react';
import { Row, Col, Form, Input, Select, Table } from 'antd';
import { classroom } from 'dump/data';

const { Option } = Select;

const columns = [
  {
    title: 'Tầng',
    dataIndex: 'floor',
    key: 'floor',
  },
  {
    title: 'Tên phòng',
    dataIndex: 'numberRoom',
    key: 'numberRoom',
  },
  {
    title: 'Sức chứa',
    dataIndex: 'capacity',
    key: 'capacity',
  },
  {
    title: 'Số sinh viên',
    dataIndex: '',
    key: 'sdv',
    render: () => <Input />,
  },
];

@Form.create({ name: 'FormSearchStudent' })
class FormSearchStudent extends Component {
  state = {};
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col span={24}>
            <Form.Item label="Tòa nhà">
              {getFieldDecorator('system5', {})(
                <Select>
                  <Option value="1">Tòa A1</Option>
                  <Option value="2">Tào B1</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={24}>
            <Table bordered dataSource={classroom} columns={columns} />
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormSearchStudent;



