import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Table, Popconfirm, Col, Modal, Input, Form } from 'antd';
import { BtnDelete, BtnAddNew, BtnSave, BtnCancel, Btn } from 'components/common/button';
import { tochucthi, classroom } from 'dump/data';
import { ColEditDelete } from 'components/common';
import FormSearchStudent from './FormSearchStudent';
import FormAddStudent from './FormAddStudent';
import FormSearchAddRoom from './FormSearchAddRoom';

const columnsRoom = [
  {
    title: 'Tòa',
    dataIndex: 'building',
    key: 'toa',
  },
  {
    title: 'Tầng',
    dataIndex: 'floor',
    key: 'floor',
  },
  {
    title: 'Tên phòng',
    dataIndex: 'numberRoom',
    key: 'numberRoom',
  },
  {
    title: 'Sức chứa',
    dataIndex: 'capacity',
    key: 'capacity',
  },
  {
    title: 'Số sinh viên',
    dataIndex: '',
    key: 'sdv',
    render: () => <Input />,
  },
  {
    title: 'Thao tác',
    dataIndex: '',
    key: 'sdv',
    render: () => <ColEditDelete />,
  },
];

const columns = [
  {
    title: 'Mã SV',
    dataIndex: 'masv',
    key: 'name',
  },
  {
    title: 'SBD',
    dataIndex: 'sbd',
    key: 'sbd',
    width: 100,
    render: () => <Input />,
  },
  {
    title: 'Họ và tên',
    dataIndex: 'name',
    key: 'nameEn',
  },
  {
    title: 'Ngày sinh',
    dataIndex: 'birthdate',
    key: 'fromScores',
  },
  {
    title: 'Tên lớp',
    dataIndex: 'className',
    key: 'toScosdfres',
  },
  {
    title: 'Phòng thi',
    dataIndex: 'phongthi',
    key: 'toScosadfres',
  },
  {
    title: 'Thao tác',
    dataIndex: 'operation',
    render: (text, record) => (
      <Fragment>
        <Popconfirm
          title="Bạn có muốn xóa?"
          okText="Xóa"
          cancelText="Hủy"
          onConfirm={() => this.handleDelete(record.key)}
        >
          <BtnDelete />
        </Popconfirm>
      </Fragment>
    ),
  },
];

const FormStartSBD = Form.create('asdfads')(props => {
  const { getFieldDecorator, getFieldValue } = props.form;
  const startSBD = getFieldValue('startSBD');
  return (
    <Form className="form-inline-label" layout="vertical">
      <Col span={12}>
        <Form.Item label="SBD bắt đầu từ">
          {getFieldDecorator('startSBD', { initialValue: '' })(<Input />)}
        </Form.Item>
      </Col>
      <Col span={4}>
        <Btn disabled={!startSBD || startSBD === ''} title="Lập SBD" />
      </Col>
    </Form>
  );
});
class ExamOrganization extends Component {
  state = { visible: false, visible2: false };
  showModal = () => {
    console.log('object');
    this.setState({
      visible: true,
    });
  };

  handleOk = e => {
    console.log(e);
    this.setState({
      visible: false,
    });
  };

  handleCancel = e => {
    console.log(e);
    this.setState({
      visible: false,
    });
  };

  showModal2 = () => {
    console.log('object');
    this.setState({
      visible2: true,
    });
  };

  handleOk2 = e => {
    console.log(e);
    this.setState({
      visible2: false,
    });
  };

  handleCancel2 = e => {
    console.log(e);
    this.setState({
      visible2: false,
    });
  };
  render() {
    return (
      <Card title="Lịch thi">
        <Row>
          <Col span={24}>
            <FormSearchStudent />
          </Col>
        </Row>
        <Row>
          <Col span={4}>
            <div className="btn-group">
              <BtnAddNew onClick={this.showModal} title="Thêm SV" />
            </div>
          </Col>
          <Col span={8}>
            <FormStartSBD />
          </Col>
          <Col span={24}>
            <Table bordered dataSource={tochucthi} columns={columns} />
          </Col>
        </Row>
        <Row>
          <Col span={5}>
            <BtnAddNew onClick={this.showModal2} title="Thêm phòng thi" />
          </Col>
          <Col span={12}>
            <Input placeholder="Số phòng ảo" />
          </Col>
          <Col span={24}>
            <Table bordered dataSource={classroom} columns={columnsRoom} />
          </Col>
        </Row>
        <Row>
          <Col style={{ textAlign: 'center' }}>
            <BtnSave />
            <BtnCancel />
          </Col>
        </Row>
        <Modal
          title="Thêm SV"
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          width="80%"
          style={{ top: 20 }}
          okText="Lưu dữ liệu"
          cancelText="Hủy"
        >
          <FormAddStudent />
        </Modal>
        <Modal
          title="Chọn phòng thi"
          visible={this.state.visible2}
          onOk={this.handleOk2}
          onCancel={this.handleCancel2}
          width="80%"
          style={{ top: 20 }}
        >
          <FormSearchAddRoom />{' '}
        </Modal>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ExamOrganization />
  </Page>
);



