import React, { Component } from 'react';
import { Row, Col, Form, Input, Select, DatePicker, Checkbox } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import YearSemesterInput from 'components/common/input/YearSemesterInput';
import { requiredRule } from '../../../utils/validateRules';

const { Option } = Select;
const optionFieldDecorator = {
  semesterId: {
    rules: [requiredRule],
  },
  yearEduId: {
    rules: [requiredRule],
  },
};
@Form.create({ name: 'FormSearchStudent' })
class FormSearchStudent extends Component {
  state = {};
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} />
          <YearSemesterInput
            optionFieldDecorator={optionFieldDecorator}
            span={5}
            form={this.props.form}
          />
          <Col span={4}>
            <Form.Item label="Lần thi">
              {getFieldDecorator('class', { initialValue: '', rules: [requiredRule] })(
                <Select defaultValue="lucy" style={{ width: 200 }}>
                  <Option value="lucy">01</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item label="Đợt thi">
              {getFieldDecorator('dt', { initialValue: '', rules: [requiredRule] })(
                <Select defaultValue="lucy" style={{ width: 200 }}>
                  <Option value="lucy">01</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item label="Ca thi">
              {getFieldDecorator('cathi', { initialValue: '', rules: [requiredRule] })(
                <Select defaultValue="lucy" style={{ width: 200 }}>
                  <Option value="lucy">01</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Ngày giờ thi" hasFeedback>
              {getFieldDecorator('birthdate', { rules: [requiredRule] })(<DatePicker showTime />)}
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item label="Tiết" hasFeedback>
              {getFieldDecorator('ffromtiet', { rules: [requiredRule] })(
                <Input style={{ width: 50 }} />
              )}
              {'=>'}
              {getFieldDecorator('totiet', { rules: [requiredRule] })(
                <Input style={{ width: 50 }} />
              )}
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item label="Học phần">
              {getFieldDecorator('hp', { rules: [requiredRule] })(
                <Select mode="multiple">
                  <Option value="1">Chủ nghĩa xã hội</Option>
                  <Option value="2">Giáo dục thể chất</Option>
                  <Option value="3">Toán rời rạc</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item className="form-item-checkbox" label="SV đủ ĐK thi">
              {getFieldDecorator('isJoined', {
                valuePropName: 'checked',
              })(<Checkbox />)}
            </Form.Item>
          </Col>
          
         
        </Row>
        <Row />
      </Form>
    );
  }
}

export default FormSearchStudent;



